<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="getSearch">
        <!--  -->
        <el-tab-pane label="交易订单" name="order">
          <OrderTable v-if="activeName === 'order'" :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage"  @refund="refund" />
        </el-tab-pane>

        <el-tab-pane label="退款订单" name="refundOrder">
          <OrderTable v-if="activeName === 'refundOrder'" :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage"/>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
  import OrderTable from '@/components/order/OrderTable'
  export default {
    components: { OrderTable },
    data() {
      return {
        activeName: 'order',
        queryForm: {
          page: 1,
          size: 10,
          total: '',
          orderNo: '',
          startDate: '',
          endDate: '',
          deviceName:'',
          orderType:0,//0、交易订单，1、退款订单
          isAll:true,
          enterpriseId:''
        },
        tableData: [],
      }
    },

    methods: {
      getPage(page) {
        this.queryForm.page = page
        this.loadData()
      },
      getSearch(params) {
        if (!this.queryForm.startDate && this.queryForm.endDate) return this.$message({ type: 'warning', message: '请选择开始日期!' }), false
        if (this.queryForm.startDate && !this.queryForm.endDate) return this.$message({ type: 'warning', message: '请选择结束日期!' }), false
        this.queryForm.total = ''
        this.queryForm.page = 1
        this.loadData()
      },
      changeDate(e, type) {
        // debugger
        console.log(this.queryForm.startDate, this.queryForm.endDate)
        if (this.queryForm.endDate && type === 'startDate' && e && e.getTime() > new Date(this.queryForm.endDate).getTime()) {
          this.$message.warning('开始时间不得大于结束时间')
          this.queryForm.startDate = ''
        } else if (this.queryForm.startDate && e && e.getTime() < new Date(this.queryForm.startDate).getTime()) {
          this.$message.warning('结束时间不得小于开始时间')
          this.queryForm.endDate = ''
        }
      },
      loadData() {
        let params = Object.assign({}, this.queryForm)
        params.startDate = params.startDate ? this.$moment(params.startDate).format('YYYY-MM-DD') : ''
        params.endDate = params.endDate ? this.$moment(params.endDate).format('YYYY-MM-DD') : ''
        params.orderType =  this.activeName === 'refundOrder' ? 1 : 0
        this.httpGet('/order/query_all/', params).then(res => {
          this.tableData = res.rows
          this.queryForm.total = res.total
        })
      },
      //退款
      refund(orderInfo){

      }
    },

    mounted() {
      this.loadData()
    }
  }
</script>

<style lang="scss" scoped>
</style>
